@import url(https://fonts.googleapis.com/css?family=Allerta+Stencil&display=swap);
html{background-image:url(https://fiverr-res.cloudinary.com/images/t_main1,q_auto,f_auto/gigs/8220332/original/219195c28800b310088a86013c87a1f0704500f7/2d-animation-and-background-art.jpg);background-size:cover}body{margin:0;padding:0;display:flex;justify-content:center;align-items:center;min-height:100vh;font-family:Arial, Helvetica, sans-serif;background-color:rgba(0,0,0,0.575)}.one{position:relative;display:inline-block;padding:15px 50px;color:#f32121;text-decoration:none;font-size:40px;overflow:hidden;-webkit-transition:0.2s;transition:0.2s;cursor:pointer;background-color:rgba(0,0,0,0);border:rgba(0,0,0,0);border-radius:6em}.one:hover{color:#842525;background:#ff0000;box-shadow:0 0 10px #f32121, 0 0 40px #f32121, 0 0 80px #f32121}.two{position:relative;display:inline-block;padding:15px 50px;color:#2ff321;text-decoration:none;font-size:40px;overflow:hidden;-webkit-transition:0.2s;transition:0.2s;cursor:pointer;background-color:rgba(0,0,0,0);border:rgba(0,0,0,0);border-radius:6em}.two:hover{color:#308425;background:#2ff321;box-shadow:0 0 10px #21f333, 0 0 40px #3af321, 0 0 80px #21f321}div{text-align:center;position:absolute;width:250px;top:140px;left:40%;margin:-50px 0 0 -150px;-webkit-animation:border-flicker 2s linear infinite;animation:border-flicker 2s linear infinite}#h2{font-family:'Allerta Stencil';color:red;font-size:140px;letter-spacing:10px;-webkit-animation:text-flicker 3s linear infinite;animation:text-flicker 3s linear infinite;margin:0px}.offset{-webkit-animation:letter-flicker 4s linear infinite;animation:letter-flicker 4s linear infinite}

